//引入vue对象
import Vue from 'vue';
//引入store对象
import Vuex from 'vuex';
//引入store的mutations状态变化方法
import mutations from './mutations'
//引入store的getters计算属性
import getters from './getters'

//显示的use一下
Vue.use( Vuex );


//导出Vuex的store实例化对象
export default new Vuex.Store({
	//设置状态
	state : {
		tasks : [
			{ name : 'vue.js', done : false },
			{ name : 'react', done : true },
		],
		choice : 'all'
	},
	// 定义store的mutations状态变化方法
	mutations,
	// 定义store的getters计算属性
	getters
});



