// 导出store的mutations状态变化方法
export default {
	//添加任务
	add(state,name){
		return state.tasks.unshift( {name, done:false} );
	},
	
	//删除任务
	remove(state,task){
		//获取点击处任务的索引值
		let index = state.tasks.findIndex( item => {
			return item == task;
		} );
		if( index !=-1 ){		//当查找到该任务时
			return state.tasks.splice( index,1 );
		}
	},
	
	//根据选择的任务完成情况，切换任务是否全部完成
	toggleState(state,task){
		//获取点击处任务的索引值
		let index = state.tasks.findIndex( item => {
			return item == task;
		} );
		if( index !=-1 ){		//当查找到该任务时
			state.tasks[index].done = !state.tasks[index].done;
		}
	},
	
	//手动切换全选与全不选
	//根据传递的全部选中和全不选切换所有任务状态
	toggleAll(state,done){
		state.tasks.forEach( item => { item.done = done } );
	},
	
	//过滤
	choose(state,txt){
		return state.choice = txt;
	},
	
	//清除已完成的任务
	clear(state){
		//过滤已完成任务，返回未完成的任务
		state.tasks = state.tasks.filter( item => { return !item.done } );
	},
	
	//根据双击处的任务对象确定任务，并传递新的文本内容
	updateEdit( state,{ task,newName } ){
		//获取点击处任务的索引值
		let index = state.tasks.findIndex( item => {
			return item == task;
		} );
		if( index !=-1 ){		//当查找到该任务时
			//修改store中的相应的任务状态
			state.tasks[index].name = newName;
		}
	}
}



