<template>
	<li class="todo " :class="{ completed:task.done, editing : editing }">
		<div class="view">
			<input type="checkbox" class="toggle" :checked="task.done" @click="toggleState(task)"> 
			<label @dblclick="editing=true">{{task.name}}</label> 
			<button class="destroy" @click="remove(task)"></button>
		</div> 
		<input type="text" class="edit" :value="task.name" v-focus="editing"
			@keyup.enter="submitEdit($event)"
			@blur="submitEdit($event)"
			@keyup.esc="cancelEdit"
		>
	</li>
</template>

<script>
	import {mapState,mapMutations} from 'vuex'
	
	export default {
		//子组件自己的私有状态
		data(){
			return {
				editing : false
			}
		},
		props : {
			task : {
				type : Object,
				required : true
			}
		},
		methods : {
			//映射store中的方法
			...mapMutations( [
				'remove',
				'toggleState',
				'updateEdit'
			] ),
			
			//取消编辑
			cancelEdit(){
				//恢复显示效果，隐藏文本框
				this.editing = false;
			},
			
			//完成编辑，通过store的mutations的方法向store中的状态提交数据
			submitEdit(e){
				//获取双击处的文本框的值
				let newName = e.target.value.trim();
				
				//通过store中mutations中的updateEdit方法向store中的状态提交数据
				if( newName ){	//当双击出现的文本框内有值时，进行更新
					this.updateEdit( { task:this.task,newName } );
				}else{		//当双击出现的文本框内没有值时，从store中删除该任务
					this.remove( this.task );
				}
				
				//恢复显示效果，隐藏文本框
				this.editing = false;
				
			}
		},
		
		//自定义指令
		directives : {
			focus(el,binding){
				//如果binding中的value为true，就获得焦点
				if( binding.value ){
					el.focus();
				}
			}
		}
	}
</script>

<style>

</style>


