// 导出store的getters计算属性
export default {
	//根据选择的过滤条件获得对应的任务
	tasks_filter(state){
		switch (state.choice){
			case 'all':			//所有任务
				return state.tasks;
			case 'active':		//未完成的任务
				return state.tasks.filter( item => { return !item.done } );
			case 'completed':	//完成的任务
				return state.tasks.filter( item => { return item.done } );
			default:
				break;
		}
	},
	
	
	//任务全选和全不选状态切换
	allState(state){
		//当有一个任务的完成度时false时，返回值为false
		return state.tasks.every( item => { return item.done } );
	},
	
	//剩余未完成的任务统计
	remaining(state){
		//筛选出所有任务中未完成的任务，以数组形式返回，获取其长度即为剩余未完成的任务数量
		return state.tasks.filter( item => { return !item.done } ).length;
	},
	
	
}



