<template>
  <div id="app">
    <section class="todoapp">
			<header class="header">
				<h1>todos</h1> 
					<input type="text" placeholder="what need to be done?" class="new-todo" @keyup.enter="add($event)">
			</header>
			<section class="main" style="">
				<input type="checkbox" class="toggle-all" :checked="allState" @click="toggleAll(!allState)"> 
				<!--<ul class="todo-list">-->
					<transition-group name="slider" tag="ul" class="todo-list">
							<li_task v-for="task in tasks_filter" :task="task" :key="task.name"></li_task>
					</transition-group>
				<!--</ul>-->
			</section>
			<footer class="footer" style="" v-show="tasks_filter.length">
				<span class="todo-count"><strong>{{remaining}}</strong> items left</span> 
				<ul class="filters">
					<li><a href="" :class="{ selected:'all'==choice }" @click.prevent="choose($event)">all</a></li>
					<li><a href="" :class="{ selected:'active'==choice }" @click.prevent="choose($event)">active</a></li>
					<li><a href="" :class="{ selected:'completed'==choice }" @click.prevent="choose($event)">completed</a></li>
				</ul>
				<button class="clear-completed" v-show="tasks.length > remaining" @click="clear">clear completed</button>
			</footer>
		</section>
  </div>
</template>

<script>
	//引入样式
	import './assets/todo-mvc.css'
	//引入定义的组件
	import Task from './components/Task'
	
	import {mapState,mapMutations,mapGetters} from 'vuex'

	//导出
	export default {
	  name: 'app',
	  //注册组件
	  components : {
	  	li_task : Task
	  },
	  
	  computed :{
			...mapState([ 
				'tasks',
				'choice'
			]),
			...mapGetters([
				'allState',
				'remaining',
				'tasks_filter'
			])
		},
		
		methods : {
			choose(e){

				this.$store.commit( 'choose',e.target.innerHTML );
			},
			add(e){
				//获取文本框内输入的任务内容，并去除内容前后的空格
				let txt = e.target.value.trim();
				
				if( txt ){		//当内容不为空时
					this.$store.commit( 'add',txt );
				}
				//清空文本框
				e.target.value = '';
			},
			...mapMutations( [
				'toggleAll',
				'clear'
			] )
			
		}
	}
</script>

<style>
	#app {
	  font-family: 'Avenir', Helvetica, Arial, sans-serif;
	  -webkit-font-smoothing: antialiased;
	  -moz-osx-font-smoothing: grayscale;
	  /*text-align: center;*/
	  color: #2c3e50;
	  margin-top: 60px;
	}
	
	.todo-list>li{
		transition:all 1s;
	}
	.slider-enter,.slider-leave-active{
		opacity:0;
		transform:translateY(-59px);
	}
	.slider-leave-active{
		position:absolute !important;
	}
	
	
</style>



