// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'		//引入vue.js文件，第三方模块，不要加 .js后缀
import app from './App'	//引入App.vue文件
import router from './router'	//引入的router目录下的index.js文件

import store from './store'		//引入store目录下的index.js文件

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,			//注入路由对象实例router
  store,			//注入vuex中的store对象实例
  template: '<app/>',	//指定模板，改变根组件（即书写模板代码）的结构
  components: { app }	//注册子组件
})



